.skill-div{
    display: grid;
    grid-template-columns: repeat(6,auto);
    grid-template-rows: repeat(auto,1fr);
    width: 60vw;
    gap: 10px;
    color: white;
    margin: auto;
    border: 1px solid white;
    
}
.skill-div>div{
    /* border: 1px solid white; */
    padding: 10px;
}
.skill-div>div>img{
width: 80px;
height: 80px;
color: white;
background-color: beige;
border-radius: 20px;
}